import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AccountItem } from '@backbase/accessgroup-common-ang';

@Component({
  selector: 'bb-data-group-items-table',
  templateUrl: './data-group-items-table.component.html',
})
export class DataGroupItemsTableComponent {
  /**
   * List of Data Group Items
   */
  @Input() items: AccountItem[] = [];

  /**
   * If enabled, last column is shown
   */
  @Input() showDeleteColumn = false;

  /**
   * Event that fires when user clicks button to delete Data Group
   */
  @Output() removeItem = new EventEmitter<AccountItem>();

  onRemoveItem(item: AccountItem) {
    if (this.showDeleteColumn) {
      this.removeItem.emit(item);
    }
  }
}
